.chat {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  height: 70px;
  border-bottom: 1px solid rgb(224, 220, 220);
}
a {
  text-decoration: none;
  color: inherit;
}
.chat__details {
  flex: 1;
}
.chat__details > p {
  color: rgb(116, 114, 114);
}
.chat__timestamp {
  color: rgb(150, 136, 136);
}
.chat__image {
  margin-right: 20px;
}
